import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import FirstRoute from '@/components/FirstRoute'
import HomebuyerView from '@/components/HomebuyerView'
import AboutView from '@/components/AboutView'
import ContactView from '@/components/ContactView'
import FaqsView from '@/components/FaqsView'
import HomesellerView from '@/components/HomesellerView'
import HomelistingView from '@/components/HomelistingView'
import StepView from '@/components/StepView'
import TestimonialsView from '@/components/TestimonialsView'
import LocalWebsiteDesign from '@/components/LocalWebsiteDesign'
import LocalEcommerceSolutions from '@/components/LocalEcommerceSolutions'
import LocalMarketingConsulting from '@/components/LocalMarketingConsulting'
import LocalPpcMarketing from '@/components/LocalPpcMarketing'
import LocalSocialMarketing from '@/components/LocalSocialMarketing'
import LocalSeoMarketing from '@/components/LocalSeoMarketing'
import LocalRetargeting from '@/components/LocalRetargeting'
import MerchantServices from '@/components/MerchantServices'
import GoogleAnalytics from '@/components/GoogleAnalytics'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/merchantservices',
      name: 'MerchantServices',
      component: MerchantServices
    },
    {
      path: '/googleanalytics',
      name: 'GoogleAnalytics',
      component: GoogleAnalytics
    },
    {
      path: '/localmarketingconsulting',
      name: 'LocalMarketingConsulting',
      component: LocalMarketingConsulting
    },
    {
      path: '/localsocialmarketing',
      name: 'LocalSocialMarketing',
      component: LocalSocialMarketing
    },
    {
      path: '/localseomarketing',
      name: 'LocalSeoMarketing',
      component: LocalSeoMarketing
    },
    {
      path: '/localretargeting',
      name: 'LocalRetargeting',
      component: LocalRetargeting
    },
    {
      path: '/localppcmarketing',
      name: 'LocalPpcMarketing',
      component: LocalPpcMarketing
    },
    {
      path: '/localwebsitedesign',
      name: 'LocalWebsiteDesign',
      component: LocalWebsiteDesign
    },
    {
      path: '/localecommercesolutions',
      name: 'LocalEcommerceSolutions',
      component: LocalEcommerceSolutions
    },
    {
      path: '/firstroute',
      name: 'FirstRoute',
      component: FirstRoute
    },
    {
      path: '/aboutview',
      name: 'AboutView',
      component: AboutView
    },
    {
      path: '/contactview',
      name: 'ContactView',
      component: ContactView
    },
    {
      path: '/faqsview',
      name: 'FaqsView',
      component: FaqsView
    },
    {
      path: '/homelistingview',
      name: 'HomelistingView',
      component: HomelistingView
    },
    {
      path: '/homesellerview',
      name: 'HomesellerView',
      component: HomesellerView
    },
    {
      path: '/stepview',
      name: 'StepView',
      component: StepView
    },
    {
      path: '/testimonialsview',
      name: 'TestimonialsView',
      component: TestimonialsView
    },
    {
      path: '/stepview',
      name: 'StepView',
      component: StepView
    },
    {
      path: '/homebuyerview',
      name: 'HomebuyerView',
      component: HomebuyerView
    }
  ]
})



